.form-group {
  margin-bottom: 1rem;
}
.form-input {
  border-color: hsl(var(--color-hsl-dark)/0.2);
  border-radius: 5px;
}
.form-input::placeholder {
  color: hsl(var(--color-hsl-dark)/0.7);
  font-size: 0.97rem;
  font-weight: var(--weight-500);
}
.form-input:focus {
  box-shadow: none !important;
}
.form-checkbox {
  min-width: 1rem;
  margin-right: 0.5rem;
}
.form-info > * {
  margin-bottom: 1rem;
}

/*
0px - 360px:		XS Phone
360px - 576px: 		Phone
576px - 768px: 		Tablet Portrait
768px - 992px:		Tablet Landscape
992px - 1200px: 	desktop
1200px - 1920px:	normal Media queries
1920px and up:  	Full HD


// 1em = 16px

	 359   =  	22.438em	
	 576   =  	36em
	 768   =	48em
	 992   =	62em
	 1200  =	75em
	 1366  =	85.375em
	 1400  =	87.5em
	 1537  =	96.063em
	 1560  =	97.5em
	 1600  =	100em
	 1680  =	105em
	 1920  =	120em
*/
:root {
  --font-family: "SF Pro Display";
  --color-orange: #FF4E00;
  --color-white: #FFFFFF;
  --color-grey: #808080;
  --color-dark: #111112;
  --color-box-shadow: #70707029;
  --color-dark-2: #222222;
  --color-dark-3: #181818;
  --color-light-grey: #F4F6F8;
  --color-hsl-dark: 240 3% 7%;
  --color-black: #000000;
  --color-white-2: rgba(255, 77, 0, 0);
  --weight-300: 300;
  --weight-400: 400;
  --weight-500: 500;
  --weight-600: 600;
  --weight-700: 700;
  --weight-800: 800;
  --weight-900: 900;
  --font-h1:3rem;
  --font-h2:2rem;
  --font-label: 0.875rem;
}

.form-label {
  font-size: var(--font-label);
  font-weight: var(--weight-500);
  color: var(--color-dark);
}

.h1 {
  font-size: var(--font-h2);
  font-weight: var(--weight-600);
}

.main-heading {
  color: var(--color-dark);
  margin-bottom: 1.5rem;
}
.main-title {
  font-size: 1.375rem;
  color: var(--color-black);
  font-weight: var(--weight-700);
  margin: 2.687rem 0 1rem;
}
.main-title--light {
  font-weight: var(--weight-500);
}

.para-text {
  font-size: 0.9375rem;
  color: var(--color-dark);
  font-weight: var(--weight-500);
}

.p-grey {
  font-size: 0.875rem;
  color: var(--color-grey);
}

.prefield-card {
  background-color: var(--color-white);
  padding: 1.875rem;
  max-width: 859px;
  margin: 3.85rem 0;
}
@media (min-width: 48em) {
  .prefield-card {
    padding: 3.75rem;
  }
}

.product-btn:hover, .btn-orange {
  background-color: var(--color-orange);
}

.reserve-links, .form-forget, .form-forget:hover, .form-register, .form-register:hover, .product-btn {
  color: var(--color-orange) !important;
}

.btn {
  padding: 0.9375rem 1.25rem;
  font-size: 1.1258rem;
  border-radius: 0.3125rem;
}
.btn-white {
  color: var(--color-dark);
  background-color: var(--color-white);
  box-shadow: 0px 12px 38px var(--color-box-shadow);
}
.btn-outline {
  border: 3px solid var(--color-orange);
  background-color: var(--color-white-2);
}
.btn-orange {
  color: var(--color-white);
  background-color: var(--color-orange);
  box-shadow: 0px 12px 38px var(--color-box-shadow);
  font-weight: var(--weight-700) !important;
}
.btn-orange:hover, .btn-orange:focus {
  color: var(--color-white) !important;
}
.btn-form-action {
  margin-bottom: 1.5rem;
  font-weight: var(--weight-700);
}
.btn-google {
  margin: 1.25rem 0;
  font-weight: var(--weight-600);
}
.btn-google .google-icon {
  display: inline-block;
  width: 1.25rem;
  margin-right: 1rem;
}
.btn-signup {
  width: 100%;
}
@media (min-width: 48em) {
  .btn-signup {
    width: 50%;
  }
}

.product {
  margin-top: 1.9375rem;
}
.product .row > * {
  padding: 0 0.625rem;
  margin-bottom: 1rem;
}
.product-wraper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0.8125rem;
  background-color: var(--color-white);
}
.product-title {
  font-size: 0.8125rem;
  margin-bottom: 0.2rem;
  color: var(--color-dark-3);
  font-weight: var(--weight-400);
}
.product-price {
  font-size: 1.125rem;
  color: var(--color-dark-3);
  font-weight: var(--weight-700);
}
.product-btn {
  padding: 0.48rem 1.688rem;
  line-height: 18.5px;
  border-radius: 0;
  font-size: 0.935rem;
  font-weight: var(--weight-600);
}
.product-btn:hover {
  color: var(--color-white) !important;
}

body {
  background-color: var(--color-light-grey);
  font-weight: var(--weight-400);
  font-family: var(--font-family);
}

.login .para-text {
  margin: 1.35rem 0;
}

.form-remember {
  color: var(--color-grey);
  font-size: 0.875rem;
}
.form-forget {
  font-size: 0.875rem;
  font-weight: var(--weight-500);
}
.form-register {
  font-size: 1rem;
  font-weight: var(--weight-600);
}
.signup .para-text {
  margin-top: 1.68rem;
}

.terms {
  margin-bottom: 1.5rem;
}
.terms-text {
  color: var(--color-grey);
  font-size: var(--font-label);
}
.terms-link {
  color: var(--color-orange);
  text-decoration: underline;
  font-weight: var(--weight-500);
}
.terms-link:hover {
  color: var(--color-orange);
}

.reserve-links {
  font-size: 0.875rem;
  font-weight: var(--weight-600);
}
.reserve-links:hover {
  color: var(--color-orange);
}
.reserve-links-edit {
  text-decoration: underline;
  font-weight: var(--weight-500);
  margin-top: 1.375rem;
}
.reserve .main-heading {
  margin: 0;
}
.reserve .info {
  margin-bottom: 0.875rem;
}
.reserve-details {
  margin-top: 1.9375rem;
}
.reserve-title {
  margin-top: 0.8125rem;
}
.reserve-email {
  margin: 0.5rem 0 0.812rem 0;
}
.reserve .text-group {
  margin: 1.562rem 0;
}

.avalible .main-heading {
  margin-bottom: 3.5rem;
}
.avalible-time {
  margin-bottom: 3.57rem;
}